// ******************* Trending ***********************//

@media screen and (min-width: 1000px) {
	.forum-side-bar {
		width: 20%;
		margin-left: 40px;
		max-height: 900px;
		background: $default-white-background; }

	.forum-trending-header {
		padding: 25px 0;
		border-bottom: 1px solid rgba(0,0,0,0.12); }

	.forum-trending-repeat {
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 0 15px;

		&.ng-move {
			transition: all .4s ease; }

		&.ng-move-active {
			@include slideInUp(); } }

	.forum-trending-component-container {
		width: 90%;
		margin: 0 auto;
		border-bottom: 1px solid rgba(0,0,0,0.12); }

	.forum-single-trending-component {
		display: flex;
		flex-direction: column;
		padding: 20px 0;
		margin-bottom: 20px;

		> div {
			text-overflow: ellipsis;
			word-break: break-all; } }

	.forum-single-trending {
		display: flex;
		flex-direction: column;
		height: 55px;
		width: 100%;

		> div {

			&:nth-of-type(2) {
				display: flex;
				align-items: stretch;
				padding: 30px 0;

				> div {

					&:nth-of-type(1) {
						display: flex;
						flex: 1;
						flex-basis: 0;
						align-items: center;
						justify-content: flex-start;

						span {
							@include open();
							font-weight: 400;
							letter-spacing: 2px;
							color: rgba(0,0,0,0.6); } }

					&:nth-of-type(2) {
						display: flex;
						flex: 1;
						flex-basis: 0;
						align-items: center;
						justify-content: flex-end;

						i {
							font-size: 24px;
							color: rgba(0,0,0,0.7);
							outline: none;
							transition: all .4s ease;

							&.subscribed {
								color: $default-green; } } } } } } } }




